import { ScoreAspect } from "../models/score-aspect";
import { Teams } from "./teams";
import { EventTeamAward } from '../models/event-team-award';

export interface State {
  eventTeamAwards: EventTeamAward[]
}

export const initialState: State = {
  eventTeamAwards: [
    // EVENT 1
    new EventTeamAward(1, Teams.SNATCH, ScoreAspect.SpiritScoreAspect),
    // FNL
    new EventTeamAward(1, Teams.HAKUNA, ScoreAspect.createFnl(9)),
    new EventTeamAward(1, Teams.BUCK, ScoreAspect.createFnl(9)),
    new EventTeamAward(1, Teams.SNATCH, ScoreAspect.createFnl(9)),
    new EventTeamAward(1, Teams.CHALK, ScoreAspect.createFnl(11)),

    // KIT
    new EventTeamAward(1, Teams.HAKUNA, ScoreAspect.TeamTopsAward),
    new EventTeamAward(1, Teams.BUCK, ScoreAspect.TeamTopsAward),
    new EventTeamAward(1, Teams.SNATCH, ScoreAspect.TeamTopsAward),
    new EventTeamAward(1, Teams.CHALK, ScoreAspect.TeamTopsAward),

    // JUDGES
    new EventTeamAward(1, Teams.HAKUNA, ScoreAspect.createJudges(7)),
    new EventTeamAward(1, Teams.BUCK, ScoreAspect.createJudges(7)),
    new EventTeamAward(1, Teams.SNATCH, ScoreAspect.createJudges(6)),
    new EventTeamAward(1, Teams.CHALK, ScoreAspect.createJudges(7)),

    // EVENT 2
    new EventTeamAward(2, Teams.BUCK, ScoreAspect.SpiritScoreAspect),
    // FNL
    new EventTeamAward(2, Teams.HAKUNA, ScoreAspect.createFnl(8)),
    new EventTeamAward(2, Teams.BUCK, ScoreAspect.createFnl(10)),
    new EventTeamAward(2, Teams.SNATCH, ScoreAspect.createFnl(9)),
    new EventTeamAward(2, Teams.CHALK, ScoreAspect.createFnl(10)),

    // KIT
    new EventTeamAward(2, Teams.HAKUNA, ScoreAspect.TeamTopsAward),
    new EventTeamAward(2, Teams.BUCK, ScoreAspect.TeamTopsAward),
    new EventTeamAward(2, Teams.SNATCH, ScoreAspect.TeamTopsAward),
    new EventTeamAward(2, Teams.CHALK, ScoreAspect.TeamTopsAward),

    // JUDGES
    new EventTeamAward(2, Teams.HAKUNA, ScoreAspect.createJudges(1)),
    new EventTeamAward(2, Teams.BUCK, ScoreAspect.createJudges(0)),
    new EventTeamAward(2, Teams.SNATCH, ScoreAspect.createJudges(0)),
    new EventTeamAward(2, Teams.CHALK, ScoreAspect.createJudges(0)),
  ]
};

export function reducer(state = initialState): State {
  return state;
}


export const getEventTeamAwardEntities = (state: State) => state.eventTeamAwards;



