import { ActionReducer, ActionReducerMap, createSelector, MetaReducer } from '@ngrx/store';
import { storeFreeze } from 'ngrx-store-freeze';

import { environment } from '../../environments/environment';

import * as fromEvents from './events';
import * as fromAthletes from './athletes';
import * as fromTeams from './teams';
import * as fromAwards from './awards';
import * as fromApp from './app';

import { Athlete, Event, Team } from '../models';
import { Builder } from "./builder";
import { AthleteScoreModel, EventScoreModel, OpenOverviewModel, TeamScoreModel, TeamTileModel } from '../view-models';
import { EventTeamAward } from '../models/event-team-award';
import { compareAthleteScoreModels } from './sort';
import { AuditRecordViewModel, TeamAuditViewModel } from '../view-models/audit';

export interface State {
  events: fromEvents.State,
  athletes: fromAthletes.State,
  teams: fromTeams.State,
  awards: fromAwards.State,
  app: fromApp.State
}

export const reducers: ActionReducerMap<State> = {
  events: fromEvents.reducer,
  athletes: fromAthletes.reducer,
  teams: fromTeams.reducer,
  awards: fromAwards.reducer,
  app: fromApp.reducer
};

export function logger(reducer: ActionReducer<State>): ActionReducer<State> {
  return function (state: State, action: any): State {

    const newState = reducer(state, action);

    return newState;
  };
}

export const metaReducers: MetaReducer<State>[] = !environment.production
  ? [logger, storeFreeze]
  : [];

export const getAppState = (state: State) => state.app;
export const getAppLoading = createSelector(getAppState, state => state.loading);

export const getEventsState = (state: State) => state.events;
export const getEventEntities = createSelector(getEventsState, fromEvents.getEventEntities);

export const getAthletesState = (state: State) => state.athletes;
export const getAthleteEntities = createSelector(getAthletesState, fromAthletes.getAthleteEntities);

export const getTeamsState = (state: State) => state.teams;
export const getTeamEntities = createSelector(getTeamsState, fromTeams.getTeamEntities);

export const getAwardState = (state: State) => state.awards;
export const getEventTeamAwardEntities = createSelector(getAwardState, fromAwards.getEventTeamAwardEntities);

const getScoreModels = (ev: Event, athletes: Athlete[], gender: "M" | "F", builder: Builder): AthleteScoreModel[] => {
  return athletes
    .filter(athlete => athlete.gender === gender)
    .map(athlete => {
      const score = athlete.getScore(ev);
      const aspects = builder.getScoreAspects(ev, athlete);
      return new AthleteScoreModel(athlete, score, aspects);
    })
    .sort(compareAthleteScoreModels);
};

export const getEventScores = createSelector(
  getEventEntities, getAthleteEntities, getTeamEntities, getEventTeamAwardEntities,
  (events: Event[], athletes: Athlete[], teams: Team[], eventTeamAwards: EventTeamAward[]) => {

    const athletesById: Map<string, Athlete> = new Map();
    athletes.forEach(athlete => athletesById.set(athlete.id, athlete));

    const teamMembers: Map<string, Athlete[]> = new Map();
    teams.forEach(team => {
      // TODO: initially athletes is completely empty, and then completely fill, consequently
      // we have to filter as well
      teamMembers.set(team.id, team.athleteIds
        .filter(athleteId => athletesById.has(athleteId)) // might be able to nuke this
        .map(athleteId => athletesById.get(athleteId)))
    });

    const builder = new Builder();
    builder.include(events, athletes, teams, eventTeamAwards);

    const result: EventScoreModel[] = events.map(ev => {
      const teamScoreModels = teams.map(team => {
        const members = teamMembers.get(team.id);
        const males = getScoreModels(ev, members, "M", builder);
        const females = getScoreModels(ev, members, "F", builder);

        // TODO: could be a lot faster. Move it into the builder.
        const awards = eventTeamAwards
          .filter(award => award.eventOrdinal === ev.ordinal && award.teamId === team.id)
          .map(award => award.award);

        //debugger;
        return new TeamScoreModel(team, males, females, awards);
      })/*.sort(compareTeamRanks)*/;

      return new EventScoreModel(ev, teamScoreModels);
    });

    return result;
  });

export const getAuditModel = createSelector(getEventScores, (eventScoreModels: EventScoreModel[]) => {
  const result: TeamAuditViewModel[] = [];

  const map: Map<string, TeamAuditViewModel> = new Map();

  eventScoreModels.forEach(eventScoreModel => {
    eventScoreModel.teams.forEach(team => {
      if (!map.has(team.team.id)) {
        map.set(team.team.id, new TeamAuditViewModel(team.team))
      }

      const model = map.get(team.team.id);

      team.scoreAspects.forEach(aspect => {
        model.records.push(new AuditRecordViewModel(eventScoreModel.event, null, aspect, model.lastRunningTotal + aspect.points));
      });

      team.females.forEach(female => {
        female.scoreAspects.forEach(aspect => {
          model.records.push(new AuditRecordViewModel(eventScoreModel.event, female.athlete, aspect, model.lastRunningTotal + aspect.points));
        });
      });

      team.males.forEach(male => {
        male.scoreAspects.forEach(aspect => {
          model.records.push(new AuditRecordViewModel(eventScoreModel.event, male.athlete, aspect, model.lastRunningTotal + aspect.points));
        });
      });
    });
  });

  map.forEach(value => {
    result.push(value);
  });

  return result.sort((a, b) => b.lastRunningTotal - a.lastRunningTotal);
});

export const getOpenOverviewModel = createSelector(getEventScores, (eventScoreModels: EventScoreModel[]) => {

  type TeamTileModelBuilder = { name: string, points: number, code: string };

  const builder = new Map<string, TeamTileModelBuilder>();

  eventScoreModels.forEach(eventScoreModel => {
    eventScoreModel.teams.forEach(team => {
      let points = team.points;
      if (builder.has(team.team.id)) {
        points += builder.get(team.team.id).points
      }

      builder.set(team.team.id, {name: team.team.name, points: points, code: team.team.code})
    });
  });


  const teams = [];
  builder.forEach((value, key) => {
    teams.push(new TeamTileModel(value.name, value.points, value.code))
  });

  teams.sort((a, b) => b.points - a.points);

  const result = new OpenOverviewModel(teams);
  return result;
});



