export * from './audit';

import { Athlete, Event, NoScore, Team } from '../models';
import { Score } from '../models/athlete';
import { ScoreAspect } from '../models/score-aspect';

export class EventScoreModel {
  public constructor(public readonly event: Event,
                     public readonly teams: ReadonlyArray<TeamScoreModel>) {
  }
}

export class TeamScoreModel {
  public constructor(public readonly team: Team,
                     public readonly males: ReadonlyArray<AthleteScoreModel>,
                     public readonly females: ReadonlyArray<AthleteScoreModel>,
                     public readonly scoreAspects: ReadonlyArray<ScoreAspect> = []
  ) {
  }

  public get points(): number {
    const maleScores = this.males.reduce((points, athlete) => points + athlete.points, 0);
    const femaleScores = this.females.reduce((points, athlete) => points + athlete.points, 0);
    const teamScores = this.scoreAspects.reduce((points, aspect) => points + aspect.points, 0);

    return maleScores + femaleScores + teamScores;
  }
}

export class AthleteScoreModel {
  public constructor(public readonly athlete: Athlete,
                     public readonly score: Score | NoScore,
                     public readonly scoreAspects: ReadonlyArray<ScoreAspect> = []
  ) {
  }

  public get points(): number {
    return this.scoreAspects.reduce((points: number, aspect: ScoreAspect) => {
      return points + aspect.points;
    }, 0);
  }

  public get isRxWinner(): boolean {
    const result = this.scoreAspects.indexOf(ScoreAspect.WonRxScoreAspect) >= 0;

    return result;
  }

  public get isScaledWinner(): boolean {
    const result = this.scoreAspects.indexOf(ScoreAspect.WonScaledScoreAspect) >= 0;

    return result;
  }
}

export class OpenOverviewModel {
  constructor(public readonly teams: ReadonlyArray<TeamTileModel>) {
  }
}

export class TeamTileModel {
  constructor(public readonly name: string,
              public readonly points: number,
              public readonly code: string) {
  }
}



