import { Event } from '../models';

const eighteenOne = new Event(1, "18.1");
const eighteenTwo = new Event(2, "18.2");
const eighteenTwoA = new Event(3, "18.2a", false);
const eighteenThree = new Event(4, "18.3");
const eighteenFour = new Event(5, "18.4");
const eighteenFive = new Event(6, "18.5");

const events = [eighteenOne, eighteenTwo, eighteenTwoA, eighteenThree, eighteenFour, eighteenFive];

export interface State {
  events: ReadonlyArray<Event>
}

export const initialState: State = {
  events: events
};

export function reducer(state = initialState): State {
  return state;
}

export const getEventEntities = (state: State) => state.events;



