// Conceptually, some of these only make sense for teams *or* individuals.
export class ScoreAspect {
  public static SpiritScoreAspect: ScoreAspect = new ScoreAspect('Team Spirit Award', 3);
  public static ScoreRecordedScoreAspect: ScoreAspect = new ScoreAspect('Athlete Score Recorded', 1);
  public static WonScaledScoreAspect: ScoreAspect = new ScoreAspect('Athlete Won Scaled Division', 1);
  public static WonRxScoreAspect: ScoreAspect = new ScoreAspect('Athlete Won RX Division', 1);

  public static TeamTopsAward: ScoreAspect = new ScoreAspect('Full Team Wearing Uniform', 1);

  public static createJudges(points: number) {
    return new ScoreAspect('Athletes Completed Judges Course', points);
  }

  public static createFnl(points: number) {
    return new ScoreAspect('Athletes Attending FNL', points);
  }

  public constructor(
    public readonly description: string,
    public readonly points: number
  ) {
  }
}



