import { Athlete, AthleteState, Score } from '../models';
import { AffiliateActions, AffiliateActionTypes } from '../actions/affiliate';
import { AthleteModel } from '../services';

export interface State {
  athletes: ReadonlyArray<Athlete>
}

export const initialState: State = {
  athletes: []
};

export const convertModel = (athleteModel: AthleteModel): Athlete => {
  const scores: Score[] = athleteModel.scores.map(score => {
    const rank = parseInt(score.rank, 10);
    const scaled = score.scaled === "1";

    return new Score(score.ordinal, rank, score.scoreDisplay, score.breakdown, score.judge, scaled);
  });

  const entrant = athleteModel.entrant;
  const division = parseInt(entrant.divisionId, 10);

  return new Athlete(entrant.competitorId, entrant.firstName, entrant.lastName, AthleteState.Loaded, division, entrant.gender, scores);
};


export function reducer(state: State = initialState, action: AffiliateActions): State {
  switch (action.type) {
    case AffiliateActionTypes.Loaded: {
      const models: AthleteModel[] = action.payload;
      const athletes = models.map(convertModel);

      return {...state, athletes};
    }
    default: {
      return state;
    }
  }
}

export const getAthleteEntities = (state: State) => state.athletes;



