import { Component } from '@angular/core';
import { select, Store } from '@ngrx/store';
import {
  getEventScores, State,
  getOpenOverviewModel, getAppLoading, getAuditModel
} from './reducers';
import { Observable } from 'rxjs/Observable';

import { Load } from './actions/affiliate';
import { EventScoreModel, OpenOverviewModel, TeamAuditViewModel } from './view-models';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  public eventScoreModels$: Observable<ReadonlyArray<EventScoreModel>>;
  public openOverviewModel$: Observable<OpenOverviewModel>;
  public auditViewModels$: Observable<TeamAuditViewModel[]>;
  public loading$: Observable<boolean>;

  constructor(private store: Store<State>) {
    this.eventScoreModels$ = this.store.pipe(select(getEventScores));
    this.openOverviewModel$ = this.store.pipe(select(getOpenOverviewModel));
    this.auditViewModels$ = this.store.pipe(select(getAuditModel));
    this.loading$ = this.store.pipe(select(getAppLoading));

    setTimeout(() => {
      this.store.dispatch(new Load(17972))
    }, 250);

  }
}



