import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Observable } from 'rxjs/Observable';
import { Action } from '@ngrx/store';
import { AffiliateService } from '../services';
import { AffiliateActionTypes, Load, Loaded } from '../actions/affiliate';

import { map, switchMap } from 'rxjs/operators';

@Injectable()
export class AffiliateEffects {
  @Effect()
  load$: Observable<Action> = this.actions$.pipe(
    ofType<Load>(AffiliateActionTypes.Load),
    map(action => action.payload),
    switchMap(affiliateId => {
      return this.affiliateService
        .get(affiliateId)
        .pipe(
          map(model => new Loaded(model))
        );
    })
  );

  constructor(private actions$: Actions, private affiliateService: AffiliateService) {
  }
}



