import { Athlete, Event, ScoreAspect, Team } from '../models';

// TODO: violating mutability.
export class TeamAuditViewModel {
  constructor(public readonly team: Team,
              public readonly records: AuditRecordViewModel[] = []) {
  }

  public get lastRunningTotal(): number {
    return !this.records.length ? 0 : this.records[this.records.length - 1].runningTotal;
  }
}

export class AuditRecordViewModel {
  constructor(public readonly event: Event,
              public readonly athlete: Athlete | null,
              public readonly aspect: ScoreAspect,
              public readonly runningTotal: number) {
  }
}



