import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { AthleteScoreModel } from '../../view-models';

@Component({
  selector: 'athlete-score',
  templateUrl: './athlete.score.component.html',
  styleUrls: ['./athlete.score.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AthleteScoreComponent {
  @Input()
  public athleteScoreModel: AthleteScoreModel;
}



