import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';

@Injectable()
export class AffiliateService {

  constructor(private http: HttpClient) {
  }

  public get(affiliateId: number): Observable<AthleteModel[]> {
    // TODO: need to manually put Nakita back in.
    const nakita: AthleteModel = {
      scores: [],
      entrant: {competitorId: 'NAKITA', firstName: 'Nakita', lastName: '', gender: 'F', divisionId: '2'}
    };

    // const fromStorage = localStorage.getItem('scores');
    // if (fromStorage) {
    //   return of(JSON.parse(fromStorage));
    // }

    const url = 'https://17f05zpuae.execute-api.us-east-1.amazonaws.com/prod/proxy?affiliates=' + encodeURIComponent(affiliateId + '');

    return this.http
      .get<ResponseContainer>(url)
      .map(data => {
        const athletes = data.affiliates[affiliateId].athletes;
        athletes.push(nakita);
        return athletes;
      })
      .do(data => {
        // localStorage.setItem('scores', JSON.stringify(data));
      });
  }
}

export type ResponseContainer = {
  affiliates: AffiliateContainer;
}

export type AffiliateContainer = {
  [id: string]: AffiliateModel;
}

export type AffiliateModel = {
  athletes: AthleteModel[]
}

export type AthleteModel = {
  entrant: EntrantModel;
  scores: ScoreModel[];
}

export type EntrantModel = {
  competitorId: string;
  firstName: string;
  lastName: string;
  gender: "M" | "F";
  divisionId: string;
}

export type Scaled = "0" | "1";

export type ScoreModel = {
  ordinal: number;
  rank: string;
  scoreDisplay: string;
  breakdown: string;
  judge: string;
  scaled: Scaled;
};



