import {  Team } from '../models';
import { Competitors } from './constants';

export class Teams {
  public static readonly SNATCH = '1';
  public static readonly CHALK = '2';
  public static readonly BUCK = '3';
  public static readonly HAKUNA = '4';
}

const teams = [
  new Team(Teams.SNATCH, "Snatch Me If You Can", Competitors.SNATCH, "snatch"),
  new Team(Teams.CHALK, "Chalk Dirty To Me", Competitors.CHALK, "chalk"),
  new Team(Teams.BUCK, "Buck Furpees", Competitors.BUCK, "buck"),
  new Team(Teams.HAKUNA, "Hakuna Masquata", Competitors.HAKUNA, "hakuna")
];

export interface State {
  teams: ReadonlyArray<Team>
}

export const initialState: State = {
  teams: teams
};

export function reducer(state = initialState): State {
  return state;
}

export const getTeamEntities = (state: State) => state.teams;



