import { Action } from '@ngrx/store';
import { AthleteModel } from '../services';

export enum AffiliateActionTypes {
  Load = '[Affiliate] Load',
  Loaded = '[Affiliate] Loaded'
}

export class Load implements Action {
  public readonly type = AffiliateActionTypes.Load;

  constructor(public payload: number) {
  }
}

export class Loaded implements Action {
  public readonly type = AffiliateActionTypes.Loaded;

  constructor(public payload: AthleteModel[]) {
  }
}

export type AffiliateActions = Load | Loaded;



