import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';

import { reducers, metaReducers } from './reducers';

import { StoreModule } from '@ngrx/store';
import { environment } from '../environments/environment';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { EffectsModule } from '@ngrx/effects';

import { AppComponent } from './app.component';
import {
  AthleteScoreComponent, EventScoreComponent, TeamScoreComponent, OpenOverviewComponent, SpiritComponent,
  AuditComponent, CreditsComponent
} from './components';
import { AffiliateEffects } from './effects/affiliate';
import { AffiliateService } from './services';

@NgModule({
  declarations: [
    AppComponent,
    EventScoreComponent,
    TeamScoreComponent,
    AthleteScoreComponent,
    OpenOverviewComponent,
    SpiritComponent,
    AuditComponent,
    CreditsComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    StoreModule.forRoot(reducers, {metaReducers}),
    StoreDevtoolsModule.instrument({
      name: 'Store DevTools',
      logOnly: environment.production,
    }),
    EffectsModule.forRoot([AffiliateEffects]),
  ],
  providers: [AffiliateService],
  bootstrap: [AppComponent]
})
export class AppModule {
}



