import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { OpenOverviewModel } from '../../view-models';

@Component({
  selector: 'open-overview',
  templateUrl: './open.overview.component.html',
  styleUrls: ['./open.overview.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OpenOverviewComponent {
  @Input()
  public openOverviewModel: OpenOverviewModel;
}



