import { Event, isScore } from "../models";
import { Team } from "../models/team";
import { Athlete, Score } from "../models/athlete";
import { ScoreAspect } from "../models/score-aspect";
import { EventTeamAward } from '../models/event-team-award';

type WinningScore = { athlete: Athlete, score: Score, scaled: boolean };

class EventWinnerBuilder {
  private winnerMap: Map<string, WinningScore> = new Map();

  constructor(private readonly event: Event) {
  }

  // TODO: should be renamed "include"
  public addAthlete(athlete: Athlete): void {
    // TODO: we may need to black list certain athletes (i.e. Liz).
    //
    if (athlete.division === 10 || athlete.division === 14)
      return;


    const score = athlete.getScore(this.event);

    if (isScore(score)) {
      const key = `${EventWinnerBuilder.typeKey(score)}/${athlete.gender}`;
      const scaled = score.scaled;

      if (!this.winnerMap.has(key)) {
        this.winnerMap.set(key, {athlete, score, scaled});
        return;
      }

      const otherScore = this.winnerMap.get(key).score;

      if (otherScore.rank > score.rank) {
        // A new winner. If they're the same, then wait?
        this.winnerMap.set(key, {athlete, score, scaled});
      }
    }
  }

  public winners(): WinningScore[] {
    const result = [];

    this.winnerMap.forEach((value, key) => {
      result.push(value);
    });

    return result;
  }

  private static typeKey(score: Score): string {
    return score.scaled ? "SCALED" : "RX";
  }
}

export class Builder {

  private eventAthleteMap: Map<string, ScoreAspect[]> = new Map();

  private teamMap: Map<string, ScoreAspect[]> = new Map();

  public include(events: Event[], athletes: Athlete[], teams: Team[], eventTeamAwards: EventTeamAward[]): void {
    // We need to find the following items:

    // const {males, females} = Builder.partitionAthletes(athletes);

    events.forEach(event => {
      const winnerBuilder = new EventWinnerBuilder(event);
      athletes.forEach(athlete => {
        winnerBuilder.addAthlete(athlete);
        if (athlete.hasScore(event) && event.scoreRecordedPoints) {
          this.add(event, athlete, ScoreAspect.ScoreRecordedScoreAspect);
        }
      });
      const winners = winnerBuilder.winners();

      winners.forEach(winner => {
        // and that would be event/athlete (could go into the same map).
        // would need to dynamically determine the winner type to score aspect, but they have the
        // same points anyway.
        let aspect = ScoreAspect.WonRxScoreAspect;
        if (winner.scaled) {
          aspect = ScoreAspect.WonScaledScoreAspect;
        }

        this.add(event, winner.athlete, aspect);
      });
    });

    // event/team-spirit

    // we don't have records for this yet, but eventually:
    // event/athlete/attendence
    // athlete/judges-course
  }

  private add(event: Event, athlete: Athlete, aspect: ScoreAspect): void {
    const key = Builder.createKey(event, athlete);
    if (!this.eventAthleteMap.has(key)) {
      this.eventAthleteMap.set(key, []);
    }

    this.eventAthleteMap.set(key, this.eventAthleteMap.get(key).concat(aspect));
  }

  private static createKey(event: Event, athlete: Athlete): string {
    return `${event.ordinal}/${athlete.id}`;
  }

  public getScoreAspectsForTeam(event: Event, team: Team): ScoreAspect[] {
    // TODO: this might include some fuckery to work for people like Yoz.
    return [];
  }

  public getScoreAspects(event: Event, athlete: Athlete): ScoreAspect[] {
    const key = Builder.createKey(event, athlete);
    let aspects = [];
    if (this.eventAthleteMap.has(key))
      aspects = this.eventAthleteMap.get(key);
    return aspects;
  }

  // public getScoreAspects(athlete: Athlete): ScoreAspect[] {
  //   // Judges course, one day.
  //  return [];
  // }
}



