import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { EventScoreModel } from '../../view-models';

@Component({
  selector: 'event-score',
  templateUrl: './event.score.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class EventScoreComponent {
  @Input()
  public eventScoreModel: EventScoreModel;
}



