import { Event } from './';

export enum Division {
  IndividualMen = 1,
  IndividualWoman = 2,

  MastersMen45_49 = 3,
  MastersWoman45_49 = 4,

  MastersMen50_54 = 5,
  MastersWoman50_54 = 6,

  MastersMen55_59 = 7,
  MastersWoman55_59 = 8,

  MastersMen60 = 9,
  MastersWoman60 = 10,

  Team = 11,

  MastersMen40_44 = 12,
  MastersWoman40_44 = 13,

  TeenageBoys14_15 = 14,
  TeenageGirls14_15 = 15,

  TeenageBoys16_17 = 16,
  TeenageGirls16_17 = 17
}

export const isScore = (score: Score | NoScore): score is Score => {
  const judge: string = (<Score>score).judge;
  // You need to have a judge to be scored, so seems like the best bet.
  return judge !== undefined && judge !== null && judge !== "";
};

export class Athlete {
  private readonly eventScoreMap: Map<number, Score> = new Map();

  public constructor(public readonly id: string,
                     public readonly firstName: string | null = null,
                     public readonly lastName: string | null = null,
                     public readonly state: AthleteState = AthleteState.Loading,
                     public readonly division: number,
                     public readonly gender: "M" | "F" = "M",
                     public readonly scores: ReadonlyArray<Score> = []) {
    scores.forEach(score => {
      this.eventScoreMap.set(score.ordinal, score);
    })
  }

  public getScore(event: Event): Score | NoScore {
    if (!this.eventScoreMap.has(event.ordinal))
      return new NoScore();
    const score = this.eventScoreMap.get(event.ordinal);
    if (!isScore(score))
      return new NoScore();
    return score;
  }

  public hasScore(event: Event): boolean {
    return isScore(this.getScore(event));
  }

  public isMale(): boolean {
    return this.gender === "M";
  }
}

export enum AthleteState {
  Loading,
  Loaded,
}

export class NoScore {
  public get isScore(): boolean {
    return false;
  }
}

export class Score {
  constructor(public readonly ordinal: number,
              public readonly rank: number,
              public readonly scoreDisplay: string,
              public readonly breakdown: string,
              public readonly judge: string,
              public readonly scaled: boolean) {
  }

  public get isScore(): boolean {
    return true;
  }
}



