import { AthleteScoreModel } from '../view-models';
import { Athlete, isScore, Score } from '../models';
import { Competitors } from './constants';

const SORT_A_FIRST = -1;
const SORT_B_FIRST = 1;
const SORT_EQUAL = 0;

export function compareAthleteScoreModels(a: AthleteScoreModel, b: AthleteScoreModel) {
  const aScore = a.score;
  const bScore = b.score;

  if (!isScore(aScore)) {
    if (!isScore(bScore)) {
      return compareDivisions(a.athlete, b.athlete);
    }

    return SORT_B_FIRST;
  }

  if (!isScore(bScore)) {
    return SORT_A_FIRST;
  }

  const divisionCompare = compareDivisions(a.athlete, b.athlete);
  if (divisionCompare !== SORT_EQUAL) {
    return divisionCompare;
  }

  const typeCompare = compareTypes(aScore, bScore);
  if (typeCompare !== SORT_EQUAL) {
    return typeCompare;
  }

  return compareRanks(aScore, bScore);
}

const getDivisionRank = (a: Athlete) => {
  // Just returning division, doesn't work, because of people like E who are in masters.
  if (a.id === Competitors.ELIZABETH_STEEDMAN || a.id === Competitors.YOZ) {
    return 1;
  }
  return 0;
};

const compareDivisions = (a: Athlete, b: Athlete): number => {
  return getDivisionRank(a) - getDivisionRank(b);
};

const compareTypes = (a: Score, b: Score): number => {
  if (a.scaled) {
    if (!b.scaled) {
      return SORT_B_FIRST;
    }

    return SORT_EQUAL;
  }

  if (b.scaled) {
    return SORT_A_FIRST;
  }

  return SORT_EQUAL;
};

const compareRanks = (a: Score, b: Score): number => a.rank - b.rank;



